import React from 'react';
import Container from '../Container';
import Img from 'gatsby-image';
import theme from '../../theme';
import Link from 'gatsby-link';

const Footer = ({ data }) =>
	<footer>
		<div className="acreditations">
			<Container>
				<h2>Well Accredited</h2>
				<div className="accreditations-content">
					<a href="http://www.chas.co.uk" target="_blank">
						<Img
							alt="Chas accredited contractor"
							resolutions={data.chas.resolutions}
						/>
					</a>
					<Img
						alt="NIC Approved"
						resolutions={data.nicApproved.resolutions}
					/>
					<Img
						alt="NIC Domestic"
						resolutions={data.nicDomestic.resolutions}
					/>
					<a href="https://www.norfolk.gov.uk/business/trading-standards/trusted-trader" target="_blank">
						<Img
							alt="Norfolk Trusted Trader"
							resolutions={data.trustedTrader.resolutions}
						/>
					</a>
					<Img
						alt="Competent Person"
						resolutions={data.competentPerson.resolutions}
					/>
				</div>
			</Container>
		</div>
		<div className="footer-content">
			<Container>
				<div className="footer-content-inner">
				<nav>
					<ul>
						<li><Link to={'/'}>Home</Link></li>
						<li><Link to={'/about'}>About</Link></li>
					</ul>
				</nav>
				</div>
			</Container>
		</div>
		<div className="site-attribution">
			<Container>
				<p>Site By: <a href="https://designspin.co.uk/website-design">Design Spin</a></p>
			</Container>
		</div>
		<style jsx>{`
			.acreditations {
				border-top: 1px solid ${theme.colors.red};
				border-bottom: 1px solid ${theme.colors.red};
			}

			.footer-content {
				overflow: hidden;
				color: ${theme.colors.white};
				background-color: ${theme.colors.grey};
			}

			.footer-content-inner {
				display: flex;
				flex-direction: row;
			}

			.footer-content-inner nav :global(a) {
				text-decoration: none;
				color: ${theme.colors.white};
			}

			.footer-content-inner nav ul {
				list-style: none;
				padding: 0;
				margin: 0;
			}

			.footer-content-inner nav ul li {
				padding: .2em 0;
				margin-bottom: 0;
				border-bottom: 1px solid ${theme.colors.white};
			}

			.footer-content-inner nav ul li:last-child {
				border-bottom: none;
			}

			.site-attribution {
				padding: .2em 0;
				text-align: center;
				overflow: hidden;
				background-color: ${theme.colors.black};
				color: ${theme.colors.white};
			}

			.site-attribution p {
				font-size: 14px;
				margin: 0;
			}

			.site-attribution a {
				text-decoration: none;
				color:${theme.colors.white};
			}

			h2 {
				margin-top: 1.45rem;
				text-align: center;
				color: ${theme.colors.red}
			}
			.accreditations-content {
				display: flex;
				justify-content: space-between;
				flex-direction: row;
				flex-wrap: wrap;
			}

			.accreditations-content :global(.gatsby-image-outer-wrapper) {
				padding: 1em;
			}
			
		`}</style>
	</footer>

export default Footer;


